.SidebarRight {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 1.287vw;
}

.department,
.staff,
.students,
.elearning,
.admission,
.finance,
.library,
.sports {
  /* width: 47%; */
  cursor: pointer;

  width: 29.5%;
}

a,
a:hover {
  text-decoration: none;
}

.qviple_about {
  margin-top: 27px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.qviple_about > p {
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
  margin: 0;
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-12);
  letter-spacing: 0.7px;
  line-height: 20px;
  text-align: center;
  color: rgba(18, 18, 18, 0.5);
}

.qviple_about > p > span:first-child {
  margin-right: 20px;
}

.qviple_logo {
  display: flex;
}

.qviple_logo > img {
  margin-left: 3px;
  margin-right: 3px;
}
.qviple_back {
  background-color: var(--bg-button);
  border: 1px solid var(--color-button);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: var(--color-button);
  cursor: pointer;
  padding-block: 0.7rem;
  padding-inline: 1rem;
  text-align: center;
  margin-block-end: 1rem;
  transition: all 300ms ease-in 50ms;
}
